body {
  background-color: #f8f8f8;
  font-family: "Ubuntu", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
  font-weight: 700;
  font-size: 4rem;
  color: #0A2E72;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  color: #000;
  font-size: 1.6rem;
  font-weight: 400;
  display: inline-block;
  text-align: center;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}

.boton-negro {
  background-color: #000;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 400;
  display: inline-block;
  text-align: center;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}

.header {
  background-color: #FFFFFF;
}

.top {
  display: none;
}
@media (min-width: 768px) {
  .top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
  }
}
.top__link {
  color: #0A2E72;
}
.top__data {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .top__data {
    flex-direction: row;
    gap: 5rem;
  }
}

.barra {
  background-color: #0088FF;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 1rem;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 1rem 0;
  }
}
.barra__accion {
  height: 100%;
}
.barra__accion--enlace {
  background-color: #0A2E72;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 400;
  display: inline-block;
  text-align: center;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .barra__accion--enlace {
    width: auto;
  }
}

.navegacion {
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    gap: 2rem;
  }
}
.navegacion__link {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .hero {
    background-image: url(../img/imagen_hero.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}
.hero__contenido {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 2rem;
  padding: 5rem;
}
@media (min-width: 768px) {
  .hero__contenido {
    flex-direction: column;
    justify-content: center;
    padding: 20rem 0;
    align-items: start;
  }
}
.hero__subtitulo {
  color: #0088FF;
  font-family: "Roboto", sans-serif;
}
.hero__titulo {
  color: #0A2E72;
  text-align: left;
}
@media (min-width: 768px) {
  .hero__titulo {
    max-width: 50%;
  }
}
.hero__texto {
  text-align: center;
  font-size: 1.4rem;
  font-family: "Roboto", sans-serif;
  margin: 0;
}
@media (min-width: 768px) {
  .hero__texto {
    max-width: 50%;
  }
}
.hero__enlace {
  background-color: #0A2E72;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 400;
  display: inline-block;
  text-align: center;
  border-radius: 3rem;
  width: 100%;
}
@media (min-width: 768px) {
  .hero__enlace {
    width: auto;
  }
}
@media (min-width: 768px) {
  .hero__enlace {
    background-color: #0A2E72;
    padding: 1rem 2rem;
    color: #FFFFFF;
    font-size: 1.6rem;
    font-weight: 400;
    display: inline-block;
    text-align: center;
    border-radius: 3rem;
    width: 20rem;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .hero__enlace {
    width: auto;
  }
}

.call {
  padding: 5rem;
  background-color: #FFFFFF;
}
.call__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .call__grid {
    flex-direction: row;
    justify-content: space-around;
  }
}
.call__heading {
  font-size: 4rem;
}
.call__enlace {
  background-color: #0A2E72;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 400;
  display: inline-block;
  text-align: center;
  border-radius: 3rem;
  width: 100%;
}
@media (min-width: 768px) {
  .call__enlace {
    width: auto;
  }
}
@media (min-width: 768px) {
  .call__enlace {
    width: 20rem;
  }
}

.departamentos {
  background-color: #f8f8f8;
  padding-top: 5rem;
}
.departamentos__heading {
  text-align: center;
  color: #0A2E72;
}
.departamentos__texto {
  text-align: center;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .departamentos__texto {
    margin: 0 10rem;
  }
}
.departamentos__grid {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .departamentos__grid {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .departamentos__grid {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.departamento {
  padding: 5rem 5rem;
  background-color: #FFFFFF;
  border: 1px solid #f8f8f8;
}
@media (min-width: 768px) {
  .departamento {
    min-width: 30rem;
    min-height: 30rem;
  }
}
@media (min-width: 1024px) {
  .departamento {
    max-width: 30rem;
    max-height: 30rem;
  }
}
.departamento__imagen {
  background-color: #EEF6FC;
  width: 13rem;
  height: 13rem;
  border-radius: 10rem;
  margin: 0 auto;
  transition: background-color;
  transition-duration: 300ms;
  display: flex;
  align-items: center;
}
.departamento__imagen:hover {
  background-color: #0A2E72;
}
.departamento__imagen img {
  width: 8rem;
  height: 8rem;
  margin: 0 auto;
}
.departamento__nombre {
  font-size: 3rem;
  color: #0A2E72;
  text-align: center;
  font-weight: 700;
}

.video {
  background-color: #FFFFFF;
}
.video__contenedor {
  background-image: linear-gradient(to bottom, #0A2E72 0%, #0A2E72 50%, transparent 50%, transparent 100%), url(../img/imagen_video.png);
  background-repeat: no-repeat;
  background-size: 100% cover;
  background-position: center bottom;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .video__contenedor {
    background-image: linear-gradient(to right, #0A2E72 0%, #0A2E72 50%, transparent 50%, transparent 100%), url(../img/imagen_video.png);
    background-position: center right;
  }
}
.video__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .video__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.video__contenido {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 5rem;
}
.video__subtitulo {
  font-size: 1.4rem;
  color: #00FFC0;
}
.video__titulo {
  color: #FFFFFF;
}
.video__enlace {
  background-color: #0088FF;
  padding: 1rem 2rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 400;
  display: inline-block;
  text-align: center;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .video__enlace {
    width: auto;
  }
}

.profesionales {
  padding: 5rem 0;
}
.profesionales__titulo {
  color: #0A2E72;
  text-align: center;
  font-size: 4rem;
  font-weight: 700;
}
.profesionales__texto {
  color: #0A2E72;
  text-align: center;
  font-size: 1.6rem;
  font-weight: 400;
}
.profesionales__grid {
  padding: 5rem 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .profesionales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .profesionales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.profesional {
  margin: 0 auto;
  border-radius: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .profesional {
    margin-bottom: 0;
  }
}
.profesional__imagen {
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
}
.profesional__contenido {
  background-color: #FFFFFF;
  border-bottom-left-radius: 1.5rem;
  border-bottom-right-radius: 1.5rem;
  padding: 1rem;
}
.profesional__nombre {
  text-align: center;
  margin: 0;
}
.profesional__area {
  text-align: center;
  font-size: 1.4rem;
  color: #0088FF;
  font-weight: 700;
  margin: 0;
}

.anexar {
  background-color: #0088FF;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}
.anexar__nombre {
  margin: 0;
  font-weight: 700;
  color: #FFFFFF;
  font-size: 2.5rem;
}
.anexar__texto {
  margin: 0;
  font-weight: 400;
  color: #FFFFFF;
  text-align: center;
  font-size: 1.6rem;
}
.anexar__icono {
  margin: 0;
  font-size: 3rem;
  padding: 1rem 2rem;
  color: #FFFFFF;
  background-color: #FF9000;
  border-radius: 5rem;
}

.contactos {
  background-color: #0088FF;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .contactos {
    padding-top: 0;
  }
}
@media (min-width: 768px) {
  .contactos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.contacto__widget {
  background-color: #FFFFFF;
  margin: 3rem 0;
  border-radius: 2rem;
  padding: 5rem;
}
.contacto__telefono {
  color: #0088FF;
  font-size: 3rem;
  display: block;
  margin-bottom: 3rem;
}
.contacto__telefono::before {
  content: "";
  background-color: #0088FF;
  background-image: url(../img/iconos/phone.svg);
  background-repeat: no-repeat;
  background-position: center;
  padding: 1.5rem 3rem;
  border-radius: 3rem;
  margin-right: 1rem;
}
.contacto__email {
  color: #0088FF;
  font-size: 3rem;
}
.contacto__email::after {
  content: "";
  background-color: #FF9000;
  background-image: url(../img/iconos/mail.svg);
  background-repeat: no-repeat;
  background-position: center;
  padding: 1.5rem 3rem;
  border-radius: 3rem;
  margin-left: 1rem;
}
.contacto__descripcion {
  color: #FFFFFF;
  font-size: 3rem;
  font-weight: 700;
}
.contacto__texto {
  color: #FFFFFF;
  font-size: 1.6rem;
}
.contacto__contenido {
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .contacto__contenido {
    padding: 5rem 0;
  }
}
.contacto__estadisticas {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 2rem;
}

.detalles {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
}
.detalles__icono img {
  width: 5rem;
}
.detalles__numero {
  margin: 0;
  font-size: 3.5rem;
  color: #FFFFFF;
}
.detalles__texto {
  margin: 0;
  font-size: 1.7rem;
  color: #FFFFFF;
}

.testimoniales {
  padding: 5rem 0;
  background-color: #FFFFFF;
}
.testimoniales__heading {
  text-align: center;
}
.testimoniales__texto {
  text-align: center;
}
.testimoniales__grid {
  display: grid;
  gap: 8rem;
  padding-top: 8rem;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .testimoniales__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.testimonio {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 2rem 5rem;
  background-color: #F7F7F7;
  box-shadow: 5px 5px 5px 10px #F7F7F7;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .testimonio {
    padding: 2rem 3rem;
  }
}
.testimonio__cabezera {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-top: -5rem;
}
.testimonio__iconos {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.testimonio__icono--cliente {
  width: 5rem;
  border-radius: 2rem;
}
.testimonio__descripcion {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}
.testimonio__nombre {
  margin: 0;
  font-weight: 700;
  color: #0A2E72;
  margin-bottom: 1rem;
}
.testimonio__cargo {
  margin: 0;
  font-size: 1.4rem;
  color: #0088FF;
}
.testimonio__titulo {
  font-size: 1.9rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.testimonio__texto {
  font-size: 1.6rem;
  line-height: 1.5;
  margin: 0;
}

.footer {
  background-color: #0A2E72;
}
.footer__grid {
  display: grid;
  gap: 2rem;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(4, 1fr);
    padding: 5rem 3rem;
  }
}
.footer__copyright {
  text-align: center;
  margin: 0;
  padding: 2rem 0;
  color: #FFFFFF;
  font-size: 1.5rem;
}

.widget__texto {
  color: #FFFFFF;
  margin: 3rem 0;
  font-size: 1.6rem;
  line-height: 1.5;
}
.widget__nombre {
  color: #FFFFFF;
  margin-bottom: 2rem;
  font-size: 700;
  font-size: 2.6rem;
}

.redes {
  display: flex;
  flex-direction: row;
